@include font('helveticaneuecyr-bold', 'helveticaneuecyr-bold-webfont');
@include font('helveticaneuecyr-roman', 'helveticaneuecyr-roman-webfont');
@include font('helveticaneuecyr-light', 'helveticaneuecyr-light-webfont');
@include font('helveticaneuecyr-medium', 'helveticaneuecyr-medium-webfont');

@mixin roman {
  font-family: 'helveticaneuecyr-roman', Arial, sans-serif; }
@mixin bold {
  font-family: 'helveticaneuecyr-bold', Arial, sans-serif; }
@mixin medium {
  font-family: 'helveticaneuecyr-medium', Arial, sans-serif; }
@mixin light {
  font-family: 'helveticaneuecyr-light', Arial, sans-serif; }


body {
  background: #FFF;
  line-height: 1.4;
  font-size: 16px;
  @include roman;
  color: $c-text;
  @include no-scroll; }

.out {
  padding-top: 90px; }

.btn {
  color: #fff;
  text-transform: uppercase;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  @include light;
  text-align: center;
  font-size: 0;
  height: 50px;

  &__title {
    font-size: 14px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    background-color: $c-accent;
    padding: 13px 10px;
    text-align: center;
    min-width: 180px; }
  &__icon {
    display: inline-block;
    background-color: #2c2c2c;
    height: 100%;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    background-color: #2c2c2c;
    width: 50px; }

  &--lg {
    .btn__title {
      min-width: 220px; } } }

.link {
  font-size: 15px;
  color: #fff;
  display: inline-block;
  padding-right: 100px;
  position: relative;
  &__title {
    text-decoration: underline; }
  &__arrow {
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: inline-block;
    &::before {
      width: 85px;
      content: '';
      position: absolute;
      height: 1px;
      background-color: $c-text;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%); }
    &::after {
      display: inline-block;
      position: absolute;
      @include icon-arrow(8, 8, 1, $c-text, 225);
      content: '';
      top: 50%;
      transform: translate(0, -50%) rotate(225deg);
      right: 0; } }
  &--green {
    color: $c-accent; } }
